<template>
  <section :style="dynamicDiv" class="d-flex flex-column justify-content-center">
    <div class="body">
      <h1 class="my-3">Decentralized financial platform</h1>
      <h3>for price-stable cryptocurrencies</h3>
      <a :href="subscriptionUrl" target="_blank" class="btn btn-secondary mt-5 mb-3">Subscribe for Updates</a>
    </div>
  </section>
</template>

<script>

export default {
  name: 'Introduction',
  data () {
    return {
      subscriptionUrl: 'https://exeum.us19.list-manage.com/subscribe?u=a412c99ae6213101c313c875c&id=c8204190ae',
      window: {
        width: 0,
        height: 0
      }
    }
  },
  created () {
    window.addEventListener('resize', this.handleResize)
    this.handleResize();
  },
  destroyed () {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize () {
      this.window.width = window.innerWidth
      this.window.height = window.innerHeight - 73
    }
  },
  computed: {
    dynamicDiv () {
      return {
        height: `${this.window.height}px`
      }
    }
  }
}
</script>

